on .mat-icon {\r\n      color: #fff;\r\n    }\r\n    .navbar-right .nav-notification-icons .mat-icon {\r\n      color: #fff;\r\n    }\r\n  }\r\n\r\n  .nav {\r\n    > li {\r\n      > a {\r\n        &:hover {\r\n          background-color: $theme-purple-nav-anchor_hover;\r\n          text-decoration: none;\r\n        }\r\n\r\n        &:focus {\r\n          background-color: $theme-purple-nav-anchor_focus;\r\n          text-decoration: none;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .bars {\r\n    color: $theme-purple-bar;\r\n  }\r\n\r\n  .sidebar {\r\n    .menu {\r\n      .list {\r\n        li {\r\n          &.active {\r\n            background-color: $theme-purple-menu-list-active;\r\n          }\r\n          a {\r\n            -moz-transition: all 0.3s;\r\n            -o-transition: all 0.3s;\r\n            -webkit-transition: all 0.3s;\r\n            transition: all 0.3s;\r\n            i,\r\n            span {\r\n              -moz-transition: all 0.3s;\r\n              -o-transition: all 0.3s;\r\n              -webkit-transition: all 0.3s;\r\n              transition: all 0.3s;\r\n            }\r\n          }\r\n        }\r\n\r\n        .ml-menu {\r\n          background-color: $theme-purple-menu-list-submenu;\r\n        }\r\n      }\r\n    }\r\n\r\n    .legal {\r\n      background-color: $theme-purple-legal-bg;\r\n\r\n      .copyright {\r\n        a {\r\n          color: $theme-purple !important;\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .breadcrumb li a {\r\n    color: $theme-purple !important;\r\n  }\r\n\r\n  .page-item.active .page-link {\r\n    background-color: $theme-purple;\r\n    border-color: $theme-purple;\r\n    color: #ffffff;\r\n    border-radius: 50%;\r\n    box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.2);\r\n  }\r\n\r\n  .btn-primary {\r\n    background-color: $theme-purple !important;\r\n    color: #fff !important;\r\n    border-color: $theme-purple !important;\r\n    &:hover {\r\n      background-color: $theme-purple !important;\r\n      color: #fff !important;\r\n    }\r\n    &:active {\r\n      background-color: $theme-purple !important;\r\n      color: #fff !important;\r\n    }\r\n    &:focus {\r\n      background-color: $theme-purple !important;\r\n      color: #fff !important;\r\n    }\r\n    &:disabled {\r\n      background-color: $theme-purple !important;\r\n      color: #fff !important;\r\n    }\r\n  }\r\n  .btn-outline-primary {\r\n    background: 0 0 !important;\r\n    color: $theme-purple !important;\r\n    border: 1px solid $theme-purple !important;\r\n    &:hover {\r\n      background: $theme-purple !important;\r\n      color: #fff !important;\r\n      border: 1px solid $theme-purple !important;\r\n    }\r\n  }\r\n  .timelineLabelColor strong {\r\n    color: $theme-purple !important;\r\n  }\r\n  .top-sidebar {\r\n    .horizontal-menu {\r\n      li {\r\n        &.active {\r\n          > a {\r\n            color: $theme-purple;\r\n          }\r\n        }\r\n        a {\r\n          -moz-transition: all 0.3s;\r\n          -o-transition: all 0.3s;\r\n          -webkit-transition: all 0.3s;\r\n          transition: all 0.3s;\r\n          &:hover {\r\n            color: $theme-purple;\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .nav-tabs > li > a:before {\r\n    border-bottom: 2px solid $theme-purple;\r\n  }\r\n}\r\n/*Logo Header Background Color*/\r\n.logo-purple {\r\n  .navbar-header {\r\n    background-color: $theme-purple;\r\n  }\r\n}\r\n","﻿//Theme white\r\n$theme-white: #fff;\r\n$theme-white-active-color: #00bcd4;\r\n$theme-white-sidemenu-active-color: #ffffff;\r\n$theme-white-legal-bg: #2c303b;\r\n$theme-white-bar: #2c303b;\r\n$theme-white-navbar-brand: #2c303b;\r\n$theme-white-navbar-brand_active: #2c303b;\r\n$theme-white-navbar-brand_hover: #2c303b;\r\n$theme-white-navbar-brand_focus: #2c303b;\r\n$theme-white-nav-anchor: #3a2c70;\r\n$theme-white-nav-anchor_hover: rgba(0, 0, 0, 0);\r\n$theme-white-nav-anchor_focus: rgba(0, 0, 0, 0);\r\n$theme-white-nav-anch