 12px;\r\n  color: #adabab;\r\n}\r\n\r\ntextarea::-webkit-input-placeholder {\r\n  font-size: 12px;\r\n  color: #adabab;\r\n}\r\n\r\ntextarea:-moz-placeholder {\r\n  /* Firefox 18- */\r\n  font-size: 12px;\r\n  color: #adabab;\r\n}\r\n\r\ntextarea::-moz-placeholder {\r\n  /* Firefox 19+ */\r\n  font-size: 12px;\r\n  color: #adabab;\r\n}\r\n\r\ntextarea:-ms-input-placeholder {\r\n  font-size: 12px;\r\n  color: #adabab;\r\n}\r\n\r\n.profile-image img {\r\n  width: 100%;\r\n}\r\n\r\n@media screen and(max-width: 1169px) {\r\n  .horizontal-layout {\r\n    .sidebar {\r\n      display: block;\r\n    }\r\n\r\n    .top-sidebar {\r\n      display: none;\r\n    }\r\n\r\n    section.content {\r\n      margin-top: 100px;\r\n    }\r\n  }\r\n}\r\n\r\n.font-icon {\r\n  display: flex;\r\n  flex-direction: row;\r\n  align-items: center;\r\n  margin-bottom: 20px;\r\n  padding: 10px;\r\n  transition: all 0.2s;\r\n\r\n  .icon-preview {\r\n    font-size: 1.8rem;\r\n    margin-right: 10px;\r\n    line-height: 1;\r\n    color: #333439;\r\n  }\r\n}\r\n\r\n.deshboard-echart-height {\r\n  height: 280px;\r\n}\r\n\r\n@media screen and(min-width: 1400px) {\r\n  .boxed-layout .container {\r\n    width: 1370px;\r\n    max-width: 100%;\r\n  }\r\n}\r\n","@mixin border-radius($radius) {\r\n  -webkit-border-radius: $radius;\r\n  -moz-border-radius: $radius;\r\n  -ms-border-radius: $radius;\r\n  border-radius: $radius;\r\n}\r\n\r\n@mixin box-shadow($shadowinfo) {\r\n  -webkit-box-shadow: $shadowinfo;\r\n  -moz-box-shadow: $shadowinfo;\r\n  -ms-box-shadow: $shadowinfo;\r\n  box-shadow: $shadowinfo;\r\n}\r\n@mixin transform($transform) {\r\n  -moz-transform: $transform;\r\n  -ms-transform: $transform;\r\n  -o-transform: $transform;\r\n  -webkit-transform: $transform;\r\n  transform: $transform;\r\n}\r\n\r\n@mixin transition($transition) {\r\n  -moz-transition: $transition;\r\n  -o-transition: $transition;\r\n  -webkit-transition: $transition;\r\n  transition: $transition;\r\n}\r\n\r\n@mixin three-dots-overflow() {\r\n  white-space: nowrap;\r\n  -ms-text-overflow: ellipsis;\r\n  -o-text-overflow: ellipsis;\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n}\r\n\r\n@mixin navbar-link-color($textcolor, $navbarcolor, $opacity) {\r\n  .navbar-brand,\r\n  .navbar-brand:hover,\r\n  .navbar-brand:active,\r\n  .navbar-brand:focus {\r\n    color: $textcolor;\r\n  }\r\n\r\n  .nav > li > a:hover,\r\n  .nav > li > a:focus,\r\n  .nav .open > a,\r\n  .nav .open > a:hover,\r\n  .nav .open > a:focus {\r\n    background-color: transparentize($navbarcolor, $opacity);\r\n  }\r\n\r\n  .nav > li > a {\r\n    color: $textcolor;\r\n  }\r\n\r\n  .bars {\r\n    float: left;\r\n    padding: 10px 20px;\r\n    font-size: 22px;\r\n    color: $textcolor;\r\n    margin-right: 10px;\r\n    margin-left: -10px;\r\n    margin-top: 4px;\r\n  }\r\n\r\n  .bars:hover {\r\n    background-color: rgba(0, 0, 0, 0.08);\r\n  }\r\n}\r\n","/*\r\n *  Document   : _demo.scss\r\n *  Author     : RedStar Template\r\n *  Description: This scss file style classes of following components\r\n\r\n            Structure (with shortcodes):\r\n\r\n            1. BUTTONS\r\n            2. CHECKBOX & RADIO\r\n            3. JQUERY KNOB-CHART\r\n            4. SWITCH\r\n            5. COLOR BOX\r\n            6. IMAGES\r\n            7. TAGS INPUT\r\n            8. GOOGLE MATERIAL DESIGN ICON\r\n            9. PRELOADER\r\n            10. ION RANGE SLIDER\r\n            11. RIGHT SIDEBAR\r\n        \r\n */\r\n\r\n/***********************************************************************\r\n\r\n\t\t\t\t\t\t[1. BUTTONS ]\r\n\t\t\t\t\t\t\r\n***********************************************************************/\r\n.demo-button-sizes {\r\n  .btn {\r\n    margin-bottom: 5px;\r\n  }\r\n}\r\n\r\n.icon-button-demo {\r\n  button {\r\n    margin-right: 5px;\r\n    margin-bottom: 12px;\r\n  }\r\n}\r\n\r\n.icon-and-text-button-demo {\r\n  button {\r\n    margin-right: 5px;\r\n    margin-bottom: 12px;\r\n    width: 16.66666666666667%;\